
  @import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Lobster&family=Pacifico&display=swap');

.Contact{
 
  background-color: #D1E7E0;
}
.quotes{
  background-color: #D1E7E0;
  text-align: center;
  padding: 1.5rem;
  color: #2B0800;
  font-size: 20px;
  margin-bottom: 1rem;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 0px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(21, 14, 14, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
  ;
  font-family: 'Dancing Script', cursive;
  font-family: 'Lobster', cursive;
  font-family: 'Pacifico', cursive;
}
.contact-container{
  width: 60%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  gap: 5rem;
}


form {
  background:  rgba(255,255,255,0.3);;
  padding: 2rem;
  height: 350px;
  border-radius: 20px;
  border-left: 1px solid  rgba(255,255,255,0.3);;
  border-top: 1px solid  rgba(255,255,255,0.3);;
  backdrop-filter: blur(10px);
  box-shadow: 20px 20px 40px -6px rgba(0,0,0,0.2);
  text-align: center;
 flex:100%;
  transition: all 0.2s ease-in-out;
  display: flex;
  flex-direction: column;
}
  
  .welcome{
    font-weight: 500;
    color: #fff;
    opacity: 0.7;
    font-size: 1.4rem;
    margin-top: 0;
    margin-bottom: 60px;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.2);
  }
  
 
  
  input,textarea {
    background: transparent;
    width: 200px;
    padding: 1em;
    margin-bottom: 2em;
    border: none;
    border-left: 1px solid rgba(255,255,255,0.3);;
    border-top: 1px solid  rgba(255,255,255,0.3);;
    border-radius: 5000px;
    backdrop-filter: blur(5px);
    box-shadow: 4px 4px 60px rgba(0,0,0,0.2);
    color: #fff;
    font-family: Montserrat, sans-serif;
    font-weight: 500;
    transition: all 0.2s ease-in-out;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.2);
    
  }
    input:hover,
    textarea:hover{
      background: rgba(255,255,255,0.1);
      box-shadow: 4px 4px 60px 8px rgba(0,0,0,0.2);
    }
    
    textarea {
      resize: none;
      overflow:hidden;
      padding: 1.5rem;
      display: flex;
      align-items: center;
   
    }
    
    input[type="button"] {
      margin-top: 10px;
      width: 150px;
      font-size: 1rem;
    }
      input:hover {
        cursor: pointer;
      }
      
      input:active {
        background: rgba(255,255,255,0.2);
      }
    
  
  
  input:hover {
    margin: 4px;
  }


::placeholder {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.4);
}




input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}
.contact-content{
  flex: 100%;
  
}
.my-Info{
  display: flex;
  flex-direction: column;
gap: 1rem;
  justify-content: space-evenly;
  user-select:text;
}

.my-Info div :first-child{
  font-weight: bolder;
}
.my-Info p{
  font-size: 20px;
}

footer{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
  background-color: #2B0800;
  padding: 2rem;
  color: #D1E7E0;
  font-size: 20px;
  margin-top: 2rem;

}
.links{
  display: flex;
  gap: 0.4rem;
}
.links a{
 
    color: #2B0800;
    background-color: #FFF5F3;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
  justify-content: center;
 
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

@media screen and (max-width:950px){
  .contact-container{
    width: 80%;
    
  }
}
@media screen and (max-width:650px){
  .contact-container{
    width: 90%;
   flex-wrap: wrap-reverse;
   gap: 0;

  }
  .quotes{
    font-size: 16px;
  }
  footer{
  
      font-size: 14px;
      margin-top: 2rem;
    
    }
    .my-Info{
  
    gap: 0.5rem;

    }
    .my-Info p{
      font-size: 15px;
    }
  
}