@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Pacifico&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Great+Vibes&family=Lobster&family=Pacifico&family=Permanent+Marker&display=swap');
.Portfolio {
    background-color: #D1E7E0;
}

/* nav section start from here */


.navbar {
    display: flex;
    padding-top: 2rem;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    margin: auto;

}



nav ul {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

nav button {
    visibility: hidden;
}

nav ul li {
    list-style: none;

}

nav ul li a {
    font-size: 20px;
    text-decoration: none;
    padding: 0.5rem 1rem;
    color: #2B0800;
    font-family: Arial, Helvetica, sans-serif;
    text-shadow: 2px 7px 5px rgba(0, 0, 0, 0.3),
        0px -4px 10px rgba(255, 255, 255, 0.3);
    ;
}


.button {
    background-color: #D1E7E0;
    border: none;
    width: 3rem;
    font-size: 20px;
}

.imp-icons {
    display: flex;
    width: 25%;
    font-size: 20px;
    justify-content: space-evenly;
}

.imp-icons .socials {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}

.imp-icons button {
    visibility: hidden;
}

.imp-icons a {
    color: #FFF5F3;
    background-color: #2B0800;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

@media screen and (max-width: 830px) {

    .navbar {
        width: 100%;
    }

    nav ul li a {
        font-size: 15px;
    }

    .imp-icons {
        font-size: 15px;
        width: 35%;
        padding-right: 2rem;

    }
}

@media screen and (max-width:650px) {
    .navbar {
        flex-direction: column-reverse;
        width: 100%;
        padding-top: 0;

    }

    nav {
        position: fixed;
        top: 0;
        left: 0;
        height: 50%;
        /* width: 100%; */
        display: flex;
        align-items: center;
        justify-content: center;

        background-color: #D1E7E0;
        transition: 1s;
        transform: translateY(-100vh);
    }

    .responsive_nav {
        transform: none;
    }

    nav button {
        visibility: visible;
    }

    nav ul {
        flex-direction: column;
        gap: 2rem;
        width: 100%;

    }

    nav ul button {
        margin-top: -6rem;
        margin-left: -2rem;

    }

    nav ul li {
        margin-right: 3rem;
    }

    .imp-icons {
        width: 100%;
        padding: 1rem 0;
        /* margin-top: 1rem; */
        padding-right: 0;
        box-shadow: rgba(0, 0, 0, 0.07) 0px 0px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(21, 14, 14, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
        ;


    }

    .imp-icons .socials {

        width: 100%;
        padding: 0 2rem;
    }

    .imp-icons button {
        visibility: visible;
    }
}

/* nav section end here */


/* Hero section start from here */
.hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    align-items: center;
    padding-bottom: 1rem;

}

.greeting {
    font-size: 30px;
    color: #ED655F;
    margin: 0;
    line-height: 0.8rem;
    font-family: 'Pacifico', cursive;
}

.Intro {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    color: #2B0800;
    font-size: 30px;
    line-height: 1rem;
    font-family: 'Lobster', cursive;
   
    font-family: 'Great Vibes', cursive;
    font-family: 'Lobster', cursive;
    font-family: 'Pacifico', cursive;
    font-family: 'Permanent Marker', cursive;


}

.Intro span {
    color: #ED655F;
}

.profile {
    width: 16rem;
    height: 16rem;
    border-radius: 50%;
    border: 4px solid #ED655F;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;

}

.profile img {
    width: 14rem;
    height: 14rem;
    border-radius: 50%;
    object-position: center;
    object-fit: cover;

}

@media screen and (max-width:650px) {
    .Intro {
        font-size: 20px;
        line-height: 2rem;
    }

    .greeting {

        margin: 0;
        line-height: 2rem;
        font-family: 'Pacifico', cursive;
    }
}