.Technologies{
  width: 100%;
  padding-bottom: 4.5rem;
  background-color: #D1E7E0;
}
.Technologies-container{
   
  padding: 0 0.5rem;
   
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(21, 14, 14, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;;
    
}
.orb  img{
    width: 60px;
    height: 60px;
   
    display: inline-block;
    margin: auto 25px;
    float: left;
    transition: all .1s ease-out;
  }
  
  .orb:hover {
  
    cursor: pointer;
  }
  

  
  .marquee {
    height: 100px;
    width: 80%;
    overflow: hidden;
    box-sizing: border-box;
    position: relative;
    margin:  auto;
   
  }
  
   .marquee--inner {
    display: block;
    width: 200%;
    margin: 30px 0;
    position: absolute;
    animation: marquee 10s linear infinite;
  } 
 
  .marquee--inner:hover {
    animation-play-state: paused;
  }
  
  /*.half {
    float: left;
    width: 10%;
  }*/
  
  .spin {
    float: left;
    width: 50%;
    
  }
   
  @keyframes marquee {
    0% { left: 0; }
    100% { left: -100%; }
  }

  @media screen and (max-width:950px) {
  
    .spin {
      float: left;
      width: 100%;
      
    }
      
  .marquee {
    height: 90px;
    width: 100%;
   
    
   
  }
    
  }