.About{
  background-color: #D1E7E0;
    width: 100%;
}
.About-image {
    width: 80%;
    display: flex;
     margin: 0 auto;
    align-items: center;
    justify-content: center;
}
.gallery{
   /* margin-top: 5%; */
    flex:100%;
    -webkit-column-count: 2;
    -moz-column-count: 2;
    column-count: 2;
}

.gallery .pics img{
    position: relative;
    border-radius: 1rem; 
    margin: 0.5rem;
  
}
.gallery > :nth-child(3){
    padding-top: 40%;
}
.About-Content{
    flex: 90%;
   margin:0 10%;
    
}
.high-light{
    font-size: 30px;
    margin: 0;
    color: #ED655F;
    font-family: 'Pacifico', cursive;
}
.main-content{
    font-family: 'Lobster', cursive;
    color: #2B0800;
}
.content{
    line-height: 2rem;
    font-size: 20px;
    color: #2B0800;
    font-weight: 400;
}
.key-features{
    display: flex;
    align-items: center;
    gap: 1rem;
    line-height: 0.1rem;
    font-size: 20px;
    color: #2B0800;
    margin: 0;
   
}
.check{
    color: #ED655F;
    filter:drop-shadow(12px 12px 7px rgba(0,0,0,0.7)) ;
    border-radius: 50%;
}
.cv-button{
    margin:2rem 0;
    padding: 1rem 3rem;
 
    border-radius: 5rem;
    cursor: pointer;
    background-color: #2B0800;
    color: #FFF5F3;
    font-size: 25px;
    font-family: 'Pacifico', cursive;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;

}
@media screen and (max-width: 830px){
    .About-image {
     flex-wrap: wrap-reverse;
    }
    .content{
        line-height: 1.5rem;
        font-size: 16px;
      
    }
    .About-Content{
        flex: 100%;
       margin:0;
        
    }
    .key-features{
       
        font-size: 18px;
       
    }
    .cv-button{
        margin:1rem 0;
        padding: 0.7rem 2.5rem;
        font-size: 18px;
    
    }

}
